body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root,
.App {
  height: 100%;
}

.wrapper {
  max-width: 50rem;
  margin: 1rem auto;
  padding: 0.2rem 1rem;

  border: 2px solid #444;
  background: rgba(#444, 0.2);
}

.pen_info {
  margin: 1rem 0;
  padding: 0 1rem;
  border: 2px solid #60a7dc;
  border-radius: 0.4rem;
  background: rgba(#60a7dc, 0.5);
}

.dropzone {
  box-sizing: border-box;
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 99999;

  background: rgba(#60a7dc, 0.8);
  border: 11px dashed #60a7dc;
}

#components-layout-demo-side .logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.3);
}

.site-layout .site-layout-background {
  background: #000;
  color: #fff;
}

.site-layout .site-layout-background h1 {
  color: #fff;
  margin-left: 2vh;
}

.site-layout .site-layout-background h2 {
  color: #fff;
  margin-left: 2vh;
}

.console-modal .ant-modal-footer {
  background-color: #1e1e1e !important;
}

.settingsbutton {
  position: fixed !important;
  bottom: 0;
  right: 0;
  margin-right: 20px;
  margin-bottom: 20px;
}

.analyzebutton {
  position: fixed !important;
  bottom: 0;
  right: 0;
  margin-right: 70px;
  margin-bottom: 20px;
}

.simulationsummary {
  position: fixed !important;
  background-color: #fff;
  border-radius: 10px;
  width: 300px;
  padding: 10px;
  top: 0;
  right: 0;
  margin-right: 10px;
  margin-top: 10px;
  opacity: 0.1;
  transition-property: opacity;
  transition-duration: 0.3s;
}

.simulationsummary.hover {
  opacity: 0.8;
}

.ant-layout-sider-children {
  height: 100%;
  overflow: auto;
}
